      <p>in the past few decades it 's become clear that sleep might be the single most important behavior that humans and other animals experience.</p>

      <p>human sleep patterns are controlled by two competing networks of chemical and electrical signals in the brain during our waking hours.</p>

      <p>neurotransmitters release deep within our cerebral cortex alert and primed for consciousness.</p>
      <p>it 's essential to life animals deprived of sleep for a long enough time will have seizures and can literally die from exhaustion.</p>

      <p>there 's no consensus on exactly why our bodies need sleep we 're definitely less active at night but considering we do a lot of cellular repair protein synthesis in general biological keep why were in dreamland but it 's not like we do that stuff when we 're awake.</p>
      <p>theory theory theory and law mean something totally different to a scientist than the way they 're used in everyday speech.</p>

      <p>we observed things everyday like that it 's bright outside when i look out the window and we often develop explanations for those observations.</p>

      <p>theory theory hypothesis and law do n't mean something different from a scientist.</p>

