<script>
import { db } from "../db";
export default {
  name: "HelloWorld",
  props: {
    msg: String
  },
  data() {
    return {
      user:{},
      documents:[],
      showSuccess:false,
      showerror:false,
      message:'',
      // name: "",
      // content1: "",
      // content2: "",
      // content3: "",
      // structure1: "",
      // structure2: "",
      // structure3: "",
      // grammar1: "",
      // grammar2: "",
      // grammar3: "",
      // words1: "",
      // words2: "",
      // words3: ""
    };
  },
  firestore: {
    documents: db.collection("name")
  },
  created: function() {
    // this.user = db.collection("documents")
    
  },

  methods: {
    makeToast(variant = null) {
        this.$bvToast.toast(this.message, {
          title: `訊息`,
          variant: variant,
          solid: true
        })
      },
    checkNoeExist(x){
      if(typeof(x) === 'undefined' || x === null){
        return true;
      }
    } , 
    save() {
      var userdata = this.user;
      var document = this.user.name;
    
      if(this.checkNoeExist(userdata.name)){
        this.message="請輸入姓名"
        this.makeToast('danger');
        return ;
      } else if(this.checkNoeExist(userdata.content1) ||this.checkNoeExist(userdata.structure1) || this.checkNoeExist(userdata.grammar1)||this.checkNoeExist(userdata.words1)){
        this.message="問卷1填寫不完全，請檢查"
        this.makeToast('danger');
        return ;
      } else if(this.checkNoeExist(userdata.content2) ||this.checkNoeExist(userdata.structure2)| this.checkNoeExist(userdata.grammar2)||this.checkNoeExist(userdata.words2)){
        this.message="問卷2填寫不完全，請檢查"
        this.makeToast('danger');
        return ;
      } else if(this.checkNoeExist(userdata.content3) ||this.checkNoeExist(userdata.structure3)|| this.checkNoeExist(userdata.grammar3)||this.checkNoeExist(userdata.words3)){
        this.message="問卷3填寫不完全，請檢查"
        this.makeToast('danger');
        return ;
      } else {
        db.collection("name")
          .doc(document)
          .set(userdata)
          .then(() => {
            // this.showSuccess=true;
            this.message="儲存成功"
            this.makeToast('success');
            console.log("user updated!");
          });
      }  
      
      
    }
  }
};
</script>

